import {
  Component,
  HostListener,
  OnInit,
} from '@angular/core';
import { element } from 'protractor';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  private offset = 53;

  navbarLinkList = [
    {
      pageName: 'Serviços',
      pageLink: '#what-we-do',
      id: 'servicesLinkId',
      active: false,
    },
    {
      pageName: 'Sobre',
      pageLink: '#about-us',
      id: 'aboutLinkId',
      active: false,
    },
    {
      pageName: 'Contactos',
      pageLink: '#contacts',
      id: 'contactsLinkId',
      active: false,
    }
  ];

  constructor() { }

  ngOnInit(): void { }

  goTo(section: string) {
    let sectionElement: HTMLElement = document.querySelector(section);
    window.scrollTo({ top: sectionElement.offsetTop - this.offset });
  }

  @HostListener('window:scroll', [])
  onWindowScroll(): void {
    let activeSectionY = Math.abs(document.documentElement.getBoundingClientRect().y) + this.offset;

    this.navbarLinkList.forEach((link, index) => {
      if(activeSectionY >= document.querySelector<HTMLElement>(link.pageLink).offsetTop && 
        (this.navbarLinkList.length === index + 1 ||
        activeSectionY < document.querySelector<HTMLElement>(this.navbarLinkList[index+1].pageLink).offsetTop)
      ) {
        link.active=true;
      } else {
        link.active=false;
      }
    })
  }
}
